import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Epic, PanelHeader, Panel, Tabbar, TabbarItem, App } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Profile from './panels/Profile';
import MapView from './panels/MapView';
import About from './panels/About';

class Example extends React.Component {
	constructor (props) {
	  super(props);
  
	  this.state = {
		activePanel: 'Persik',
	  };
	  this.onStoryChange = this.onStoryChange.bind(this);
	}
  
	onStoryChange (e) {
	  this.setState({ activeStory: e.currentTarget.dataset.story })
	}
  
	render () {
  
	  return (
		<Epic activeStory={this.state.activeStory} tabbar={
		  <Tabbar>
			<TabbarItem
			  onClick={this.onStoryChange}
			  selected={this.state.activeStory === 'persik'}
			  data-story="persik"
			  text="Новости"
			></TabbarItem>
			<TabbarItem
			  onClick={this.onStoryChange}
			  selected={this.state.activeStory === 'discover'}
			  data-story="discover"
			  text="Поиск"
			></TabbarItem>
			<TabbarItem
			  onClick={this.onStoryChange}
			  selected={this.state.activeStory === 'messages'}
			  data-story="messages"
			  label="12"
			  text="Сообщения"
			></TabbarItem>
			<TabbarItem
			  onClick={this.onStoryChange}
			  selected={this.state.activeStory === 'notifications'}
			  data-story="notifications"
			  text="Уведомлен."
			></TabbarItem>
			<TabbarItem
			  onClick={this.onStoryChange}
			  selected={this.state.activeStory === 'more'}
			  data-story="more"
			  text="Ещё"
			></TabbarItem>
		  </Tabbar>
		}>
		  <View id="feed" activePanel="feed">
			<Panel id="feed">
			  <PanelHeader>Feed</PanelHeader>
			</Panel>
		  </View>
		  <View id="discover" activePanel="discover">
			<Panel id="discover">
			  <PanelHeader>Discover</PanelHeader>
			</Panel>
		  </View>
		  <View id="messages" activePanel="messages">
			<Panel id="messages">
			  <PanelHeader>Messages</PanelHeader>
			</Panel>
		  </View>
		  <View id="notifications" activePanel="notifications">
			<Panel id="notifications">
			  <PanelHeader>Notifications</PanelHeader>
			</Panel>
		  </View>
		  <View id="more" activePanel="more">
			<Panel id="more">
			  <PanelHeader>More</PanelHeader>
			</Panel>
		  </View>
		</Epic>
	  )
	}

	
	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	
componentDidMount() { 
	connect.subscribe((k) => { 
	   switch (k.detail.type) { 
	case 'VKWebAppFlashGetInfo': 
		  this.setState({ hm: k.detail.data }); 
	   break; 
			 default: 
	console.log(k.detail.type); 
} 
}); 
connect.send("VKWebAppFlashGetInfoResult",{} );
}
	 componentDidMount() { 
             connect.subscribe((k) => { 
                switch (k.detail.type) { 
             case 'VKWebAppFlashSetLevelResult': 
                   this.setState({ hm: k.detail.data }); 
                break; 
                      default: 
             console.log(k.detail.type); 
        } 
      }); 
	  connect.send("VKWebAppFlashSetLevel", {"level": 0});
     }


	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to });
		window.history.pushState( { panel: e.currentTarget.dataset.to }, `${e.currentTarget.dataset.to}` );
	};

	ChangeSearch (e) { 
		this.setState({ search:e.replace(/\s+/g,' ') });
		this.maps();
	}

	maps() {
		let api = `https://geocode-maps.yandex.ru/1.x/?format=json&geocode=${this.state.search}`
		fetch(api)
		.then(res => res.json())
        .then(data => this.setState({
            list: data.response.GeoObjectCollection.featureMember
        }))
		.catch(e => console.log(e))
	}

	view = (e) => {
		this.setState({ pos: e.GeoObject.Point.pos.split(' ', 2).reverse() });
	}

	menu (e) {
		if(e.state) {
			this.setState( { activePanel: e.state.panel } );
		} else {
			this.setState( { activePanel: 'home', search: '' } );
			window.history.pushState( { panel: 'home' }, `home` );
		}
	}

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Persik id="persik" go={this.go} />
				<Profile id="profile" go={this.go} />
				<MapView id="view" fetchedUser={this.state.fetchedUser} go={this.go} state={this.state} />
				<About id="about" go={this.go} />
				</View>
		);
	}
	}
export default App;
